    <!-- Q2 -->
    <div v-if="step===2" class="house-2-box">
      <div class="house-2-text">剛到怪奇博物館完成報到的你，正好奇著該怎麼進行你的任務，此時你聽到旁邊一陣打鬥聲⋯⋯</div>
      <div @click="goToQ3" class="house-2-triangle-box">
        <div class="house-2-triangle"></div>
      </div>
    </div>
    <!-- Q3 -->
    <div v-if="step===3" class="house-0-box">
      <img class="house-0-ghost" src="@/assets/images/pre/pre-man-black.png" alt="ghost">
      <div class="house-0-talk-box">
        <div class="house-0-head">中年男子</div>
        <div class="house-0-frame">
          <div>吉拿棒！你打不贏我的，快投降吧。告訴我其他的怪奇都躲在什麼地方？</div>
          <div @click="goToQ4" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Q4 -->
    <div v-if="step===4" class="house-0-box">
      <img class="house-0-ghost" src="@/assets/images/pre/pre-gg-black.png" alt="ghost">
      <div class="house-0-talk-box">
        <div class="house-0-head">？？？</div>
        <div class="house-0-frame">
          <div>要我說幾次啊，館長⋯⋯我的名字是「吉古拉」！還有我絕對不會告訴你怪奇們在哪的！你這個瘋狂的中年大叔！</div>
          <div @click="goToQ5" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Q5 -->
    <div v-if="step===5" class="house-0-box">
      <img class="house-0-ghost" src="@/assets/images/the-man-1.png" alt="ghost">
      <div class="house-0-talk-box">
        <div class="house-0-head">館長</div>
        <div class="house-0-frame">
          <div>該死！吉娃娃，你難道不知道，放任這些怪奇在城市裡遊蕩，會對世界造成嚴重的危害嗎？你再不老實點，我會讓你後悔當一隻怪奇的！</div>
          <div @click="goToQ6" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
