    <!-- Q6 -->
    <div v-if="step===6" class="house-0-box">
      <img class="house-0-ghost" src="@/assets/images/ending/ending-gg-normal.png" alt="ghost">
      <div class="house-0-talk-box">
        <div class="house-0-head">吉古拉</div>
        <div class="house-0-frame">
          <div>就說了我是吉古拉⋯⋯怪奇對世界才沒有危害呢，是對你們人類來說才算是危害吧！算了，這時候大家應該已經逃到安全的地方了吧？我也要溜之大吉了！哈哈哈～</div>
          <div @click="goToQ7" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Q7 -->
    <div v-if="step===7" class="house-0-box">
      <!-- <img class="house-0-ghost" src="@/assets/images/ending/ending-gg-normal.png" alt="ghost"> -->
      <div class="house-0-talk-box">
        <div style="visibility: hidden;" class="house-0-head">吉古拉</div>
        <div class="house-0-frame">
          <div>？</div>
          <div @click="goToQ8" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Q8 -->
    <div v-if="step===8" class="house-0-box">
      <img class="house-0-ghost" src="@/assets/images/the-man-1.png" alt="ghost">
      <div class="house-0-talk-box">
        <div class="house-0-head">館長</div>
        <div class="house-0-frame">
          <div>等等！可惡，這傢伙原來剛剛都是在拖延時間⋯⋯（注意到你的存在）咦，怎麼有民眾跑進來？怪奇博物館今天不開放參觀唷，陽明海洋文化藝術館也是。</div>
          <div @click="goToQ9" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
