          <div>噢，我都差點忘了我自己有找人來幫忙，原來是你呀。本來應該先讓你進行一個月的訓練，不過現在沒有時間，你就直接上吧。你已經知道什麼是「怪奇」了嗎？</div>
          <div @click="goToQ11" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Q11 -->
    <div v-if="step===11" class="house-0-box">
      <img class="house-0-ghost" src="@/assets/images/the-man-1.png" alt="ghost">
      <div class="house-0-all-box">
        <div @click="goToQ12" class="house-0-all-btn">不知道，請告訴我。</div>
        <div @click="goToQ14" class="house-0-all-btn">知道了，我可以出發了嗎？</div>
