        <div class="house-0-head">館長</div>
        <div class="house-0-frame">
          <div>那麼我簡短說明吧，咳咳。怪奇就是這世界上不乾淨的能量匯集、附著在人、事、物上時，變成的邪惡生物。他們生來就必須被收容在怪奇博物館的結界裡，才不會對世界帶來危害。最近博物館遭到攻擊，有許多怪奇逃了出來，包含你剛剛看到的那隻吉⋯⋯吉祥物。</div>
          <div @click="goToQ13" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Q13 -->
    <div v-if="step===13" class="house-0-box">
      <img class="house-0-ghost" src="@/assets/images/the-man-1.png" alt="ghost">
      <div class="house-0-talk-box">
        <div class="house-0-head">館長</div>
        <div class="house-0-frame">
          <div>其中有「４隻」怪奇特別危險，找到他們，並且帶回博物館，就是你最重要的任務目標。這些話如果你忘記了，之後可以點選介面左上的「筆記」圖示來查看。點選介面中的「圖鑑」圖示則可以確認你目前捕捉到了哪些怪奇，記得至少要帶「３隻怪奇」回來。</div>
          <div @click="goToQ14" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Q14 -->
    <div v-if="step===14" class="house-0-box">
      <img class="house-0-ghost" src="@/assets/images/the-man-1.png" alt="ghost">
      <div class="house-0-talk-box">
        <div class="house-0-head">館長</div>
        <div class="house-0-frame">
          <div>出發前，最後提醒你一件事：雖然這些怪奇都有著人形，但你不要忘了，他們「不是人類！」他們是純粹被本能驅使的邪惡生物，所以不擇手段捕捉他們就對了，可別對怪奇心軟。</div>
          <div @click="goToQ15" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Q15 -->
    <div v-if="step===15" class="house-0-box">
      <div class="house-0-talk-box">
        <div class="house-0-frame-2">
          <div>館長匆匆忙忙地離去後，你一邊想著他說的話，一邊思考<span style="color: #95ff00;">「基隆市街地圖」</span>上標記的<span style="color: #95ff00;">「４個怪奇區域」</span>，該先去哪個。此時你注意到旁邊陰影處有「人」對你招了招手，你走過去一看⋯⋯</div>
          <div @click="goToQ16" class="house-0-triangle-box">
            <div class="house-0-triangle-2"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Q16 -->
    <div v-if="step===16" class="house-0-box">
      <img class="house-0-ghost" src="@/assets/images/ending/ending-gg-normal.png" alt="ghost">
      <div class="house-0-talk-box">
        <div class="house-0-head">吉古拉</div>
        <div class="house-0-frame">
          <div>館長終於走了⋯⋯欸欸，可以跟你說一下話嗎？</div>
          <div @click="goToQ17" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Q17 -->
    <div v-if="step===17" class="house-0-box">
      <img class="house-0-ghost" src="@/assets/images/the-man-1.png" alt="ghost">
      <div class="house-0-all-box">
        <div @click="goToQ1801" class="house-0-all-btn">是吉拿棒！我要報告館長！</div>
        <div @click="goToQ1802" class="house-0-all-btn">你是吉古拉，對嗎？</div>
      </div>
    </div>
    <!-- Q18-1 -->
    <div v-if="step===18.1" class="house-0-box">
      <img class="house-0-ghost" src="@/assets/images/ending/ending-gg-normal.png" alt="ghost">
      <div class="house-0-talk-box">
        <div class="house-0-head">吉古拉</div>
        <div class="house-0-frame">
          <div>你才吉拿棒，你全家吉拿棒！我是「吉古拉」！而且報告館長什麼的，是小學生唷⋯⋯</div>
          <div @click="goToQ19" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Q19 -->
    <div v-if="step===19" class="house-0-box">
      <img class="house-0-ghost" src="@/assets/images/ending/ending-gg-normal.png" alt="ghost">
      <div class="house-0-talk-box">
        <div class="house-0-head">吉古拉</div>
        <div class="house-0-frame">
          <div>你冷靜聽我說一下，我知道突然要你一個「人類」幫助怪奇什麼的，也許太難接受。我不會阻止你去捕捉怪奇的，但至少我希望你在動手之前，可以想一想「這真的是最好的方法嗎？」也許你會發現，怪奇跟人類，其實沒那麼不同啊⋯⋯</div>
          <div @click="goToQ21" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Q18-2 -->
    <div v-if="step===18.2" class="house-0-box">
      <img class="house-0-ghost" src="@/assets/images/ending/ending-gg-normal.png" alt="ghost">
      <div class="house-0-talk-box">
        <div class="house-0-head">吉古拉</div>
        <div class="house-0-frame">
          <div>啊⋯⋯你、你竟然記得我的名字！嗚嗚，我好感動唷。大家都一直叫錯我的名字，去餐廳點菜的時候也只把我當小菜，明明我這麼優秀的呀⋯⋯</div>
          <div @click="goToQ20" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Q20 -->
    <div v-if="step===20" class="house-0-box">
      <img class="house-0-ghost" src="@/assets/images/ending/ending-gg-normal.png" alt="ghost">
      <div class="house-0-talk-box">
        <div class="house-0-head">吉古拉</div>
        <div class="house-0-frame">
          <div>啊，對不起我太激動了。不過如果是你來找怪奇的話，看來我們還有點希望。我不會阻止你去捕捉怪奇的，但至少我希望你在動手之前，可以想一想「這真的是最好的方法嗎？」。如果你試著傾聽怪奇的聲音，也許事情可以和平解決，你會有「意想不到的收穫」也說不定呢。</div>
          <div @click="goToQ21" class="house-0-triangle-box">
            <div class="house-0-triangle"></div>
          </div>
        </div>
      </div>
    </div>
    <!-- Q21 -->
    <div v-if="step===21" class="house-0-box">
      <div class="house-0-talk-box">
        <div @click="goToNext" class="house-0-ending-btn">緝捕怪奇</div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'PageIndex',
  components: {

  },
  data () {
    return {
      step: 1
    }
  },
  mounted () {

  },
  methods: {
    goToQ2 () {
      this.step = 2
    },
    goToQ3 () {
      this.step = 3
    },
    goToQ4 () {
      this.step = 4
    },
    goToQ5 () {
      this.step = 5
    },
    goToQ6 () {
      this.step = 6
    },
    goToQ7 () {
      this.step = 7
    },
    goToQ8 () {
      this.step = 8
    },
    goToQ9 () {
      this.step = 9
    },
    goToQ10 () {
      this.step = 10
    },
    goToQ11 () {
      this.step = 11
    },
    goToQ12 () {
      this.step = 12
    },
    goToQ13 () {
      this.step = 13
    },
    goToQ14 () {
      this.step = 14
    },
    goToQ15 () {
      this.step = 15
    },
    goToQ16 () {
      this.step = 16
    },
    goToQ17 () {
      this.step = 17
    },
    goToQ1801 () {
      this.step = 18.1
    },
    goToQ1802 () {
      this.step = 18.2
    },
    goToQ19 () {
      this.step = 19
    },
    goToQ20 () {
      this.step = 20
    },
    goToQ21 () {
      this.step = 21
    },
    goToNext () {
      this.$store.commit('changeNotDotHead', true)
      this.$store.commit('changeHeader', true)
      this.$store.commit('switchPre', true)
      // 塞入 local storage
      let vuesList = JSON.parse(localStorage.getItem('vuexList'))
      vuesList.isPassPre = true
      localStorage.setItem('vuexList', JSON.stringify(vuesList))
      //
      this.$router.push({
        path: './mission-note'
      })
    }
  }
}
</script>
<style lang="scss" scoped>

  .index-house {
    min-height: 100vh;
    padding-top: 100px;
    background: linear-gradient(#000 70%, #95ff00);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0px;

    .house-1 {

      &-box {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-top: 26vh;
      }

      &-img {
        width: 50%;
        height: auto;
      }

      &-btn {
        padding: 6px 14px;
        background-color: #95ff00;
        font-size: 20px;
        margin-top: 80px;
        border-radius: 30px;
      }
    }

    .house-2 {

      &-box {
        margin-top: 40vh;
        padding: 0px 20px;
      }

      &-text {
        color: #95ff00;
        font-size: 18px;
      }

      &-triangle-box {
        margin-top: 36px;
      }

      &-triangle {
        margin: auto;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 15px 15px 0 15px;
        border-color: white transparent transparent transparent;
      }
    }

    .house-0 {

      &-box {
        position: relative;
        width: 100%;
        min-height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-repeat: no-repeat;
        background-size: contain;
        background-position-x: center;
        background-position-y: top;
        background-image: url('~@/assets/images/pre/pre-yang-min.png');
      }

      &-ghost {
        width: 100%;
        height: auto;
        position: absolute;
        top: 0px;
      }

      &-talk-box {
        display: flex;
        flex-direction: column;
      }

      &-head {
        padding: 4px 16px;
        margin: 150px auto 0px;
        text-align: center;
        background: #95ff00;
        color: black;
        border-radius: 30px;
        opacity: 0.7;
        z-index: 1;
      }

      &-frame {
        width: 100vw;
        margin-top: 10px;
        max-height: 200px;
        padding: 20px;
        color: white;
        overflow: scroll;
        color: black;
        background: #95ff00;
        z-index: 1;
        opacity: 0.7;
      }

      &-triangle-box {
        margin-top: 16px;
      }

      &-triangle {
        margin: auto;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 15px 15px 0 15px;
        border-color: black transparent transparent transparent;
      }

      &-frame-2 {
        margin-top: 20vh;
        color: white;
        padding: 8px;
      }

      &-triangle-2 {
        margin: auto;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 15px 15px 0 15px;
        border-color: white transparent transparent transparent;
      }

      &-ending-btn {
        padding: 6px 14px;
        background-color: #95ff00;
        font-size: 20px;
        margin-top: 80px;
        border-radius: 30px;
      }

      // zero step false

      &-all-box {
        position: absolute;
        width: 100vw;
        height: 100vh;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
        background-color: rgba(0, 0, 0, 0.8);
        padding-top: 25vh;
        z-index: 1;
      }

      &-all-btn {
        width: 100%;
        color: white;
        text-align: center;
        background: gray;
        margin-top: 30px;
        padding: 20px 10px;
      }
    }
  }
</style>



